
@import url('https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:wght@400&display=swap');
@import url("https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic");

.App{
  height: 100%;
  width: 100%;
  margin: 8px;
}

/* unvisited link */
a:link {
  color: yellowgreen;
}

/* visited link */
a:visited {
  color: green;
}

/* mouse over link */
a:hover {
  color: yellow;
}

/* selected link */
a:active {
  color: darkolivegreen;
}

.menu {
  position: sticky;
  top: 0;
  z-index: 6;
  display: flex;
  align-self: flex-start;
  justify-self: center;
  flex-direction: row;
  align-items: flex-start;
  margin-top: -8rem;
  transition: 50ms ease-in;
  color: #034584;
  margin-left: 1rem;
  margin-right: 1rem;
  font-size: 22px;
}

#helsebergen{
  z-index: 3;
  height: 130px;
  width: auto;
  top: 0;
  display: flex;
  align-self: end;
  position: sticky;
  margin-right: 3rem;
}

#icon {
  color: white;
}

.menu h3{
  transition: 50ms ease-in;
  color: #034584;
  margin-left: 1rem;
  margin-right: 1rem;
  font-size: 22px;;
}

.menu h3:hover {
  color: black;
  transform: scale(1.1);
  margin-left: 1.5rem;
  cursor: pointer;
}


.shownmenuicons {
  margin-left: 1rem;
}

#menuarrow {
  opacity: 1;
  transition: 200ms;
  filter: invert(100%);
  cursor: pointer;
}

.menuarrowclicked #menuarrow{
  filter: alpha(opacity=0);
  cursor: pointer;
}

button {
  background-color: transparent;
  border: none;
}

#ShownMenu {
  z-index: 2;
  padding: 5rem;
  background-color: #e4eff9;
  background-color:rgb(255, 255, 255);
  min-height:100vh;
  transition: all .4s ease-in-out;
  transform: translate(-40%, 0);
  text-align: left;
  opacity: 0%;
}

.menuclicked #ShownMenu {
  transform: translate(0%, 0);
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  background-color: rgb(93, 174, 225);
  background-color: rgb(135, 206, 250,0.94);
  background-color: rgb(255, 255, 255, 0.95);
  opacity:100%;
}

h1{
  font-size: 60px;
  margin: 0em;
  padding: 0.2em;
  transition: all 500ms;
  /**color: #f8f8f2;*/
  color: black;
  color: #f8f8f2;
}

.subtitle {
  font-size: 30px ;
  font-style:"italic";
}

.Headline h1 {
  background-color: rgba(255, 255, 255, 0.6);
  color: rgb(0, 0, 0);
  font-weight: 600;
}

.hovertext {
  margin-top: 30vh;
}

