.boxshadow{

  box-shadow:
  1.2px 3.3px 2.1px rgba(0, 0, 0, 0.026),
  2.8px 7.7px 4.9px rgba(0, 0, 0, 0.037),
  5px 13.8px 8.8px rgba(0, 0, 0, 0.046),
  8.3px 23px 14.6px rgba(0, 0, 0, 0.054),
  13.8px 37.8px 24.1px rgba(0, 0, 0, 0.063),
  24px 66.1px 42.1px rgba(0, 0, 0, 0.074),
  52px 143px 91px rgba(0, 0, 0, 0.1)
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  width: 100%;
}

/** Header */
#infoguide{
  color: grey;
  margin: 10px;
  letter-spacing: 1px;
  font-family:'Fira Sans Condensed', sans-serif;
}

.Headline {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%
}

.HeaderBackgroundImage {
  position: absolute;
  top: 0; 
  left: 0; 
  z-index: -3; 
  mask-image: linear-gradient(to bottom, rgb(255, 255, 255) 60%, rgba(252, 251, 251, 0) 100%);
  opacity: 90%;
}

.FadeBottom {
  mask-image: linear-gradient(to bottom, rgba(0,0,0,1) 90%, rgba(0,0,0,0) 100%);
}

.FadeTop {
  mask-image: linear-gradient(to top, rgba(0,0,0,1) 90%, rgba(0,0,0,0)100%);
}

.Fade {
  mask-image:  linear-gradient(to bottom, rgba(0,0,0,0), rgba(255, 254, 254, 1), rgba(255, 255, 255, 1), rgba(0,0,0,1), rgba(0,0,0,0));
}

.LightFade {
  mask-image: linear-gradient(to top, rgb(255, 255, 255) 90%, rgba(255, 255, 255)100%);
}

#headline{
  letter-spacing: 3px;
  margin-bottom: 25px;
  /**font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-family: sans-serif, Helvetica, sans-serif;
  */
  font-family: 'Fira Sans Condensed', sans-serif;
  -webkit-text-stroke: 1px white;
  color: rgb(69, 69, 75);
  font-size: 100px;
}

.Header-written {
  width: 100%;
}

.App-header {
  height:min-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  background-color: rgb(17, 17, 17, 0.5);
}

/** Top Navigation */
.TopNavElements {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 20px;
  margin-bottom: 0px;
  width: fit-content;
  color: darkslategrey;
  font-size: small;
  width: 100%;
}

.TopNavImages {
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  border-color: darkolivegreen
}

/** Main Content */
.Content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  font-size: 25px;
  width: 100%;
  position: fixed;
}

.Main-content{
  width: 100%;
}

/** Section */
.SectionDescription {

  font-family: 'Fira Sans Condensed';
  font-style: normal;
  font-weight: 500;
  font-size: 150%;
  line-height:80px;
  text-align: center;
  margin-bottom: 0;
}

.SectionHeader{
  width: 100%;
}


/** Posts */
.Post {
  padding-top: 3rem;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction:column;
  flex-grow: inherit;
  width: 100%;
  margin-bottom: 3rem;
}


.LeftTextRightImage {
  display: flex;/**skifte rekkefølge*/
}

.LeftImageRightText {
  display: flex;
  flex-direction: column; /**skifte rekkefølge*/
  justify-content: center;
  margin-top: 5rem;
  margin-bottom: 5rem;
  margin-left: 2rem;
  margin-right: 0rem;
}

.breadtext span{
  font-size:33px;
  font-weight: 300;

}
.breadtext {
  font-size:33px;
  font-weight: 300;
}

.aase {
  font-size: 40px;
  font-weight: bold;
  font-style: italic;
}

/** Testimonies */
.Testimonies {
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.TestimoniesImgSpan img{
  width: 10rem;
  border-radius: 100%;
  margin-bottom: 4rem;
  opacity: 0.3;
  transition: transform .8s ease;
  background-color: white;
}

.TestimoniesImgSpan img:hover{
  opacity: 1;
  cursor: pointer;
}

.TestimoniesImgSpan{
  display: grid;
  grid-template-columns: 33% 33% 33%;
  width: 40rem;
}

.TestimoniesParSpan {
  width: 30rem;
  color: white;
}

fieldset {
  text-align: center;
  font-size: 30px;
  height: 26rem;
  position: relative;
}

legend {
  font-size: 40px; 
}

.testimoniestext{
  font-style: italic;
}


.testimoniestitle{
  text-align: end;
  position:absolute;
  bottom:0;
  right:1rem;
  
}

/** Regular Article. Used to inform. */
.Article {
  display: flex;
  justify-content: center;
  align-content: center;
  text-align: left;
}

.Article p{
  margin-top: 0rem;
  font-size: 26px;
  width: 55rem;
  margin-bottom: 2rem;
}

.Article img {
  /**width: 55rem;*/
  width: 35rem;
  margin-bottom: 4rem;
  box-shadow: rgba(0, 0, 0, 0.75) 0px 5px 15px;
}


.ArticleContent{
  display: flex;
flex-direction: column;
align-items: center;
}

.ArticleSideBySideContent{
  display: flex;
  flex-direction: row-reverse;
  justify-content:flex-start;
}

.ArticleSideBySideContent p{
  margin-left: 3rem;
  width: 40rem;
}

/** Testimonies */
.TestimonyImage img{
  height: 6.25rem;
  width: 6.25rem;
  border-radius: 100%;
}


/** Article with sticky image on side. Used to tell a story */
.ArticleStickyImage {
  display: flex;
  justify-content: center;
  text-align: left;
}

.StickyHeader{
  text-align:left;
  margin-left: 13rem;
  font-size:65px;
}


#sound{
  height: 4rem;
  width: auto;
  transition: transform 0.4s ease;
  margin-left: 3rem;
  margin-right: -8rem;
  margin-top: 2rem;
  position: sticky;
  filter: brightness(1);
  top: 6rem;
  padding-bottom: 4rem;
}


#sound:hover{
  filter: brightness(1.2);
  transform: scale(1.1);
}

.ArticleStickyImage p{
  margin-top: 0rem;
  font-size: 27px;
  width: 60rem;
  /**
  color:rgb(204, 203, 203);*/
  max-width: 70%;
  line-height: 1.5;
}


.Information {
 max-width: 100%; 
}


.ZigZag {
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: stretch;
  align-content: flex-start;
}

.ZigZag img {
  width: 400px;
  height: 600px;
  object-fit: cover;
  filter: drop-shadow(8px 8px 10px rgb(5, 4, 4));
  box-shadow: rgba(0, 0, 0, 0.75) 0px 5px 15px;
}

.ZigZag p {
  margin-top: 1rem;
  width: 400px;
}

.FirstZig {
  max-width: 50%;
  padding-right: 1rem;
}
.SecondZig {
  margin-top: 20rem;
  max-width: 50%;
}

.LeftImageRightText p {
  margin: 0rem;
  padding: 0rem;
  max-width: 100%;
  
}

.LeftImageRightText img {
  opacity: 1;
  justify-self: center;
  align-self: center;
  transition: transform 0.4s ease;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.75) 0px 5px 15px;
}

.LeftImageRightText img:hover{
  transform: scale(1.02);
}

img{
  width: 100%;
  height: auto;
  -webkit-transition: opacity 1s ease-in-out;
}



.PostContent {
  display: flex;
  justify-content: center;
}

.NotScroll {
  display: flex; 
  flex-direction: column;
  justify-content: center;
  align-content: center;
}
.NotScroll p {
  top: 200px;
}


.LowOpacity{
  opacity: 0.7;
}

.BackgroundPostImage{
  height:100%;
  position: absolute; /* sticky sidetekst*/
  top:0;
}

.SidePostImage {
  height: 100%;
  width: 40vw;
  top: 100px;
  display: flex;
  height:100%;
  position: sticky;
  justify-content: center;
  align-content: center;
  margin-top: 5vh;
}


/** Side navigation */

.Side_navigation{
  display: flex;
  flex-direction: column;
  margin: 40px;
}

.Category{
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 10px;
}

#category_0{
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
}
#category_5{
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px;
}

.Category-subheaders{
  display: flex;
  flex-direction: column;
  align-items: flex-start;

}

.Category-subheaders p:hover {
  font-weight: bold;
}

.OneImage {
  width: 50%;
}

.SeveralImages {
  width: 30%;
}

