@import url('https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:wght@300&family=Fira+Sans:ital,wght@0,400;1,500&display=swap');

@import url("https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic");
body{
  max-width: 100%;
  font-family: 'Fira Sans Condensed', sans-serif;
  overflow-x: hidden; 
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
background-image: linear-gradient(to right top, #000000, #080607, #070607, #040406, #000000, #04070a, #020405, #0f202b, #050815, #0d0c0f, #000000, #080607);
}
.App {
  margin: 0;
  font-family:  'Fira Sans Condensed', sans-serif, w -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

code {
  font-family:'Fira Sans Condensed', sans-serif, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
